@font-face {
    font-family: Quicksand;
    src: url(../public/Quicksand-VariableFont_wght.ttf);
}

@font-face {
    font-family: Imprima;
    src: url(../public/Imprima-Regular.ttf);
}

@font-face {
    font-family: DancingScript;
    src: url(../public/DancingScript-VariableFont_wght.ttf);
    }

@import './style/variables.scss';

*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

html{
    font-size: 62.5%;
    @media (max-width: 1280px) {
        font-size: 57%;
    }
    @media (max-width: 1170px) {
        font-size: 50%;
    }
    @media (max-width: 562px) {
        font-size: 35%;
    }
}

body{
    font-family: Quicksand, sans-serif;
    overflow-x: hidden;
    a:link, a:visited{
        text-decoration: none;
    }
    .color-yellow{
        color: $color-secondary;
    }
    .color-green{
        color: $color-primary;
    }
    .color-blue{
        color: $color-tertiary;
    }
    .color-red{
        color: $color-quaternary;
    }

    &::-webkit-scrollbar {
        width: 15px;
    }
    &::-webkit-scrollbar-track {
    background: $color-white-1;
        width: 15px;
    }
    &::-webkit-scrollbar-thumb {
    background: $color-primary;
    }
    &::-webkit-scrollbar-thumb:hover {
        background: $color-primary-dark;
    }
    .hidden{
        opacity: 0;
        visibility: hidden;
        width: 0;
        height: 0;
        display: none;
    }

    
}
::selection{
    border-radius: 5px;
    background-color: $color-primary;
    color: white;
}

.dark-mode{
    color: $color-white-1;
}

.bg-dark-p-color{
    background-color: $color-dark-mode-primary;
}
.bg-dark-s-color{
    background-color: $color-dark-mode-secondary;
}