@import '../../style/variables.scss';

$width-navigation: 15rem;
$height-navigation: 32rem;

.navigation-bar{
    z-index: 1000;
    position: fixed;
    width: $width-navigation;
    right: -$width-navigation / 2;
    top: $width-navigation / 2;
    height: $height-navigation;
    padding: 25px 10px;
    background-color: $color-secondary;
    border-radius: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .icon{
        display: inline-block;
        padding: 5px 0px;
        margin-right: auto;
        .cls-1{
            fill: $color-white-1;
        }
        &:hover{
            cursor: pointer;
        }
    }
}
.navigation{
    display: none;
}

@media  (max-width: 768px) {
    .navigation-bar{
        opacity: 0;
        visibility: hidden;
        width: 0;
        height: 0;
        display: none;
        
    }
    .navigation {
        display: block;
        &__checkbox {
            display: none;
        }
    
        &__button {
            background-color: $color-white-1;
            height: 8rem ;
            width: 8rem;
            position: fixed;
            top: 8rem ;
            right: 6rem ;
            border-radius: 50%;
            z-index: 5000;
            box-shadow: 0 1rem * 1.5 3rem * 1.5 rgba($color-black-1, .2);
            text-align: center;
            cursor: pointer;
        }
    
        &__background {
            height: 6rem;
            width: 6rem;
            border-radius: 50%;
            position: fixed;
            top: 9rem ;
            right: 6.5rem ;
            background-image: radial-gradient($color-primary, $color-primary-dark);
            z-index: 1000;
            transition: transform .8s cubic-bezier(0.86, 0, 0.07, 1);
        }
    
        &__nav {
            height: 100vh;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 1500;
    
            opacity: 0;
            width: 0;
            transition: all .8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
        }
    
        &__list {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            list-style: none;
            text-align: center;
            width: 100%;
        }
    
        &__item {
            margin: 1rem * 1.5;
        }
    
        &__link {
            &:link,
            &:visited {
                opacity: 0;
                width: 0;
                height: 0;
                visibility: hidden;
                display: none;
            }
            
            &:hover,
            &:active {
                background-position: 100%;
                color: $color-primary;
                transform: translateX(1rem * 1.5);
            }
        }
    
    
        //FUNCTIONALITY
        &__checkbox:checked ~ &__background {
            transform: scale(80);
        }
    
        &__checkbox:checked ~ &__nav {
            opacity: 1;
            width: 100%;
        }

        &__checkbox:checked ~ &__nav &__link{
            opacity: 1;
            width: auto;
            height: auto;
            visibility:visible;
            display: inline-block;
            font-size: 3rem * 1.5;
            font-weight: 300;
            padding: 1rem * 1.5 2rem * 1.5;
            color: $color-white-1;
            text-decoration: none;
            text-transform: uppercase;
            background-image: linear-gradient(120deg, transparent 0%, transparent 50%, $color-white-1 50%);
            background-size: 220%;
            transition: all .4s;

            span {
                margin-right: 1.5rem * 1.5;
                display: inline-block;
            }
        }
    
    
        //ICON
        &__icon {
            position: relative;
            margin-top: 4.2rem;
    
            &,
            &::before,
            &::after {
                width: 3rem * 1.5;
                height: 2px;
                background-color: $color-black-2;
                display: inline-block;
            }
    
            &::before,
            &::after {
                content: "";
                position: absolute;
                left: 0;
                transition: all .2s;
            }
    
            &::before { top: -.8rem * 1.5; }
            &::after { top: .8rem * 1.5; }
        }
    
        &__button:hover &__icon::before {
            top: -1rem * 1.5;
        }
    
        &__button:hover &__icon::after {
            top: 1rem * 1.5;
        }
    
        &__checkbox:checked + &__button &__icon {
            background-color: transparent;
        }
    
        &__checkbox:checked + &__button &__icon::before {
            top: 0;
            transform: rotate(135deg);
        }
    
        &__checkbox:checked + &__button &__icon::after {
            top: 0;
            transform: rotate(-135deg);
        }
    }
}